import Vue from 'vue'
import Router from 'vue-router'
const HelloWorld  = ()=>import('@/components/HelloWorld')
const Got552211  = ()=>import('@/components/template1')
const BZ120156  = ()=>import('@/components/template2')
const SW01216  = ()=>import('@/components/template3')
const zx84682  = ()=>import('@/components/template4')
// import Got552211 from '@/components/template1'
// import BZ120156 from '@/components/template2'
// import SW01216 from '@/components/template3'
// import zx84682 from '@/components/template4'
Vue.use(Router);
export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component:HelloWorld
    },
    {
      path: '/Got552211',
      name: 'Got552211',
      component: Got552211
    },
    {
      path: '/BZ120156',
      name: 'BZ120156',
      component: BZ120156
    },
    {
      path: '/SW01216',
      name: 'SW01216',
      component: SW01216
    },{
      path: '/zx84682',
      name: 'zx84682',
      component: zx84682
    }
  ]
})



